import React, { useEffect, useState } from 'react';
import axios from 'axios';
import store from 'store';
import { backendUrl } from '../../config/config';
import { useTranslation } from 'react-i18next';
import { useCustomStore } from '../../context/useStore';
import { Helmet } from 'react-helmet';
import {string} from "prop-types";

import './index.scss';
import CustomFoodBlock from "../../components/CustomFoodBlock";
import HeadingBlock from '../../components/HeadingBlock';
import MobileFilters from '../../components/MobileFilters/MobileFilters';
import useFetch from "../../api/useFetch";
import Spinner from "../../components/Spinner";
import ErrorIndicator from "../../components/Errors/ErrorIndicator";

const PageWrapper = React.memo((props) => {
    const {t} = useTranslation();
    const [ response, error, isLoading ] = useFetch(props.url);
    const [ filteredGoods, setFilteredGoods ] = useState([]);
    const [ loading, setLoading ] = useState(false);
    const [ err, setErr ] = useState(false);

    const { state } = useCustomStore();


    const createJSONLD = () => {
        const data = {
            '@context': 'https://schema.org/',
            "@type": "Offer",
            "category": props.title,
            'brand': '24ROLLS',
            'description': `${props.title} Доставка. Заказывай ${props.title} с бесплатной доставкой в Днепре, Запорожье и Харькове✔️${props.title} от 15грн ☎ 050 719-24-24 ✔️ Онлайн заказ еды на дом,офис.`,
            'name': props.title,
            'itemOffered': filteredGoods.map(el => ({
                '@type': 'Product',
                'category': props.title,
                'material': props.title,
                'productID': el._id,
                'slogan': el.name_ru,
                'name': el.name_ru,
                'brand': {
                    '@type': 'Organization',
                    'name': '24ROLLS',
                    'address': 'Днепр, Запорожье',
                    'location': 'Днепр, Запорожье'
                },
                'description': el.description_ru,
                'image': el.image,
                'offers': {
                    '@type': 'Offer',
                    'offeredBy': {
                        '@type': 'Organization',
                        'name': '24ROLLS',
                        'address': 'Днепр, Запорожье',
                        'location': 'Днепр, Запорожье'
                    },
                    'seller': {
                        '@type': 'Organization',
                        'name': '24ROLLS',
                        'address': 'Днепр, Запорожье',
                        'location': 'Днепр, Запорожье'
                    },
                    'category': props.title,
                    'priceCurrency': 'UAH',
                    'price': el.price
                }
            }))
        };

        return JSON.stringify(data);
    }

    useEffect(() => {

        const cityShop = store.get('24rolls').customOptions.city;

        if (response) {
            const goodsWithCity = response.filter(el => {
                let valid = false;
                if (cityShop === 'Dnipro') {
                    el.shop.map(el => {
                        if (el.name === 'Dp') valid = true;
                    });
                }
                else if (cityShop === 'Zaporijja') {
                    el.shop.forEach(el => {
                        if (el.name === 'Zp' ) valid = true;
                    });
                }
                else if (cityShop === 'Kharkov') {
                    el.shop.forEach(el => {
                        if (el.name === 'Kh' ) valid = true;
                    });
                }
                
                return valid;
            });
            setFilteredGoods(goodsWithCity);
        }

        if (state.filterMode) {
            setLoading(true);
            axios.get(backendUrl + `/products/filter/category/${props.categoryId}/${state.filterMode}`)
                .then(res => {
                    setFilteredGoods(res.data);
                    setErr(false);
                })
                .catch(err => {})
                .finally(() => setLoading(false));
        }
    }, [ state.filterMode, response ]);
    
    return (
        <div className={'rolls_container'}>
            <Helmet>
                <script async type="application/ld+json">
                    {createJSONLD()}
                </script>
                <title>{'24ROLLS | ' + props.title}</title>
                        <meta
                            name="description"
                            content={`${props.title} Доставка. Заказывай ${props.title} с бесплатной доставкой в Днепре, Запорожье и Харькове✔️${props.title} от 15грн ☎ 050 719-24-24 ✔️ Онлайн заказ еды на дом,офис.`}
                        />
                        <meta
                            name="keywords"
                            content="Заказать суши, роллы, wok, пасту, салаты, супы, напитки, доставка, Днепр, Запорожье, Харьков, вкусно, быстрая доставка, акции, скидки, выгодные предложения, подарки, блог и статти кулинарии, как приготовить суши, круглосуточная доставка суши, 24ROLLS, rolls, 24rolls.zp.ua"
                        />
                        <meta property="twitter:title" content="ROLLS24" />
                        <meta property="og:title" content="ROLLS24" />
                        <meta
                            name="twitter:description"
                            content={`${props.title} Доставка. Заказывай ${props.title} с бесплатной доставкой в Днепре, Запорожье и Харькове✔️${props.title} от 15грн ☎ 050 719-24-24 ✔️ Онлайн заказ еды на дом,офис.`}
                        />
                        <meta
                            name="og:description"
                            content={`${props.title} Доставка. Заказывай ${props.title} с бесплатной доставкой в Днепре, Запорожье и Харькове✔️${props.title} от 15грн ☎ 050 719-24-24 ✔️ Онлайн заказ еды на дом,офис.`}
                        />
                        <script async type="application/ld+json">
                            {createJSONLD()}
                        </script>
                        <script type="application/ld+json">
                            {JSON.stringify({ "@context": "http://schema.org", "@type": "WebSite", "@id": "#website", "url": "https://24rolls.zp.ua/", "name": "\u0421\u0443\u0448\u0438 24Rolls Dnepr", "potentialAction": { "@type": "SearchAction", "target": "https://24rolls.zp.ua/?s={search_term_string}", "query-input": "required name=search_term_string" } })}
                        </script>
                        <script type="application/ld+json">
                            {JSON.stringify({ "@context": "http://schema.org", "@type": "Organization", "url": "https://24rolls.zp.ua/", "contactPoint": [{ "@type": "ContactPoint", "telephone": "068 719 24 24", "contactType": ["customer service", "sales"], "areaServed": ["UA"], "contactOption": ["TollFree"], "availableLanguage": ["Russian", "Ukrainian"] }], "sameAs": ["https://www.youtube.com/channel/UCmJwKEidDraJtVdYtpnsJSA", "https://www.instagram.com/24rolls.com.ua/"] })}
                        </script>
            </Helmet>
            {!err && !error &&
                <>
                    <HeadingBlock title={t(props.title)} svg={true} h={'h1'} />
                    {<MobileFilters filters={props.filters} />}
                </>
            }
            {( isLoading || loading ) && <Spinner /> }
            {(error || err ) && !isLoading && !loading && <ErrorIndicator />}
            {
                filteredGoods 
                && !isLoading 
                && !loading 
                &&  !err
                && !error &&
                    <div className={'rolls_container_block'}>
                        {
                            filteredGoods.map(el =>
                                <CustomFoodBlock 
                                {...props} 
                                style={'open-modal'} 
                                key={el._id} 
                                food={el} 
                                />
                            )
                        }
                    </div>
            }
            {
                !filteredGoods[0] 
                && !isLoading 
                && !loading 
                &&  !err
                && !error &&
                <p> {'Нет данных товаров...'} </p>
            }
        </div>
    )
});

PageWrapper.propTypes = {
    title: string.isRequired,
    url: string
}

export default PageWrapper;